.nb-map {
    overflow: hidden;
}

.map-dropdown {
    background-color: $turquise-light;
    margin: 0;

    .uk-dropdown.uk-open {
        box-shadow: none;
        border-top: 1px solid $gray-warm;
    }

    *+.uk-grid-margin,
    .uk-grid+.uk-grid,
    .uk-grid>.uk-grid-margin {
        margin-top: 15px;
    }

    .dropdown-close-button {
        position: absolute;
        top: 20px;
        right: 40px;
        z-index: 1;
    }

    ul {
        position: relative;
        z-index: 0;
    }

    li {
        list-style-type: none;

        >button {
            border-left: 1px solid $gray-mid;
            display: inline-flex;
            align-items: center;
            padding: 24px 25px;
            position: relative;

            &::after {
                position: absolute;
                content: font-char($chevron-down-thin);
                color: $blue;
                right: 35px;
                font-size: 24px;
                top: 50%;
                transform: translateY(-50%);
                font-size: 8px;
                font-family: 'svgicons';
            }

            &::before {
                margin-right: 15px;
            }
        }

        &:first-of-type {
            >button {
                padding-right: 52px;
                padding-left: 40px;

                &::before {
                    font-size: 23px;
                }
            }
        }


        &:last-of-type>button {
            border-right: 1px solid $gray-mid;
            padding-right: 41px;

            &::after {
                right: 25px;
            }
        }
    }

    .nb-checkbox {
        opacity: 1;
        font-family: $font-default;
    }
}

.map-select-btns {
    .uk-button-secondary {
        background-color: $blue;
    }

    .uk-button-text {
        min-height: 38px;
        padding: 0 30px;

        svg {
            width: 138px;
        }

        .shape {
            width: 136px;
            stroke-dasharray: 82 403;
            stroke-dashoffset: -648;
        }

        .font-close {
            display: flex;
            flex-direction: row-reverse;
            align-items: center;
            font-weight: $bold;
            margin-left: 0;

            &::after {
                display: none;
            }

            &::before {
                font-size: 10px;
                color: inherit;
                margin-left: 15px;
                font-weight: $bold;
            }
        }
    }

    @media (min-width: $breakpointSmall) {
        .uk-button-secondary {
            margin-left: 38px;
        }
    }
}

.map {
    height: 620px;
    max-height: 620px;
    width: 100%;
}

.map-pins {
    z-index: 2;

    //probably needs to be deleted - just li style
    li {
        position: absolute;
        left: 50%;
        top: 50%;

        &:nth-of-type(2) {
            left: 75%;
            top: 75%;
        }
    }

    button {
        background-image: url('../../img/union-small.png');
        background-size: 45%;
        background-position: 60% 45%;
        background-repeat: no-repeat;
        clip-path: url(#mapPin);
        position: absolute;
        top: 40%;
        left: 50%;
        background-color: $blue;
        width: 86px;
        height: 95px;

        &::after {
            position: absolute;
            content: font-char($eye);
            font-family: 'svgicons';
            top: 40%;
            left: 49%;
            transform: translate(-50%, -50%);
            font-size: 14px;
            color: $white;
        }

        @include hover {
            transform: scale(1.2);
        }
    }
}

.map-totals {
    z-index: 1;

    li {
        background-color: $green;
        border-radius: 50%;
        color: $white;
        height: 40px;
        left: 50%;
        list-style-type: none;
        position: absolute;
        top: 50%;
        width: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0 10px 30px rgba($blue, .3);

        &:nth-of-type(2) {
            left: 75%;
            top: 75%;
        }
    }
}
